import { extendObservable, runInAction } from 'mobx'
import axios from 'axios'

class UserStore {
	constructor() {
		extendObservable(this, {
			loading: true,
			isLoggedIn: false,
			username: '',
			list: []
		})
	}

  login = async () => {
    try {
      let res = await axios({
        url: '/isLoggedIn',
        method: 'post',
        headers: { 
          'Accept': 'application/json', 
          'Content-Type': 'application/json' 
        }
      })
      
      let result = await res.data
      
      if (result && result.success) {
        runInAction(() => {
          this.loading = false
          this.isLoggedIn = true
          this.username = result.username
          this.getListItems()
        })  
      } else {
        runInAction(() => {
          this.loading = false
          this.isLoggedIn = false
        })
      }
      
    } catch(err) {
      runInAction(() => {
        this.loading = false
        this.isLoggedIn = false
      })
    }
  }

	getListItems = async () => { 
    try {
      let res = await axios({
        url: '/list',
        method: 'get',
        params: {
          username: this.username
        }
      })

      let result = await res.data

      if (result && result.success) {
      	runInAction(() => {
        	this.loading = false
        	this.list = result.data
      	})
      } else {
      	runInAction(() => {
        	this.loading = false
        })
      }
    } catch (err) {
    	runInAction(() => {
	      this.loading = false
    	})
    }
  }
}

export default new UserStore()