import React, { useState, useEffect } from 'react'
import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import Typography from '@material-ui/core/Typography'
import Link from '@material-ui/core/Link'
import Tag from './Tag'
import AddButton from './AddButton'
import { makeStyles } from '@material-ui/core/styles'
import formatDate from '../utils/formatDate.js'
import axios from 'axios'
import DOMPurify from 'dompurify'
import UserStore from '../stores/UserStore'
import { observer } from 'mobx-react' 

const useStyles = makeStyles(theme => ({
  root: {
    display: 'flex',
    margin: 10,
  },
  details: {
    display: 'flex',
    flexDirection: 'column',
  },
  content: {
    flex: '1 0 auto',
    maxWidth: 1000,
  },
  chip: {
    margin: 10,
  },
}))

function Article({ article, setTagList }) {
  const classes = useStyles()
  const {
    id,
    name,
    description,
    datePublished,
    tags,
    owningOrgId,
    sourceUrl,
  } = article
  const sanitizedName = { __html: DOMPurify.sanitize(article.name) }
  const [isBtnDisabled, setIsBtnDisabled] = useState(false)

  async function addToList() {
    try {
      let res = await axios({
        url: '/list',
        method: 'post',
        headers: {
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
        data: JSON.stringify({
          username: UserStore.username,
          listID: id,
          name: name,
          sourceUrl: sourceUrl
        }),
      })

      let result = await res.data

      if (result && result.success) {
        UserStore.loading = false
        setIsBtnDisabled(true)
        UserStore.list.push(result)
      } else {
        UserStore.loading = false
      }
    } catch (err) {
      UserStore.loading = false
    }
  }

  const chips = tags.map(tag => <Tag key={tag.id} tag={tag} setTagList={setTagList} />)

  return (
    <Card className={classes.root} variant="outlined">
      <div className={classes.details}>
        <CardContent className={classes.content}>
          <Typography variant="h5" component="h2" gutterBottom>
            <Link
              href={sourceUrl}
              target="blank"
              color="inherit"
              underline="none"
              dangerouslySetInnerHTML={sanitizedName}
            />
          </Typography>
          <Typography color="textSecondary" gutterBottom>
            {owningOrgId ? `${owningOrgId} • ${formatDate(datePublished)}` : formatDate(datePublished)}
          </Typography>
          {chips}
          <Typography>{description}</Typography>
          {UserStore.isLoggedIn && <AddButton addToList={addToList} isBtnDisabled={isBtnDisabled} />}
        </CardContent>
      </div>
    </Card>
  )
}

export default observer(Article)